:root {
	--max-width: 580px;
	--main-color: #41cd8c;
	--second-color: #08aeea;
}
@media (max-width: 780px) {
	:root {
		--max-width: 100vw;
	}
}
body {
	margin: 0;
	background-color: rgb(245, 245, 245);
	direction: rtl;
	font-family: "Cairo", sans-serif;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	overscroll-behavior-y: contain;
}

/************** MainPage **************/
.MainPage {
	position: relative;
	margin: auto;
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	padding-bottom: 95px;
	text-align: center;
	opacity: 0;
	transition: opacity 500ms;
}
#header {
	position: sticky;
	z-index: 1;
}
#Gradient1 stop:nth-child(1) {
	stop-color: var(--main-color);
}
#Gradient1 stop:nth-child(2) {
	stop-color: var(--second-color);
}
#title {
	position: fixed;
	top: 25px;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 2;
	color: white;
	margin: 0;
	margin-top: -5px;
	font-size: calc(20px + 6 * ((100vw - 320px) / 680));
}
.remainValues {
	display: flex;
	justify-content: space-evenly;
	width: 100%;
	position: absolute;
	top: 37%;
	transform: translate(0, -50%);
}
.caloriesRemain,
.caloriesEaten {
	margin: auto;
	text-align: center;
	font-weight: bold;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	color: white;
	width: 100px;
}
.caloriesRemain p,
.caloriesEaten p {
	margin: 0px;
	margin-bottom: 0px;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
}
.caloriesRemain p:nth-child(1),
.caloriesEaten p:nth-child(1) {
	font-family: sans-serif;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
}
#macrosPercent {
	position: absolute;
	width: 100%;
	display: flex;
	justify-content: space-around;
	color: white;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
.macrosPercentContainer {
	position: relative;
	width: 27%;
	height: 8px;
	border-radius: 20px;
	background-color: rgb(250, 250, 245, 0.5);
}
.macrosPercentLine {
	position: absolute;
	right: 0px;
	top: 0px;
	height: 8px;
	width: 0;
	border-radius: 20px;
	background-image: linear-gradient(-225deg, #fffeff 0%, #d7fffe 100%);
	transition: width 350ms;
}
.macroName {
	position: absolute;
	top: -5px;
	left: 50%;
	transform: translate(-50%, -100%);
	margin: 0px;
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.percentNum {
	margin: 0;
	margin-top: 12px;
	font-family: sans-serif;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
}
.percentNum span {
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
}
#addButton {
	position: sticky;
	margin-left: 10px;
	bottom: 85px;
	float: left;
	background-color: var(--main-color);
}
.circleButton {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	z-index: 3;
	cursor: pointer;
}
.floatButton {
	position: fixed;
	bottom: 60px;
	transform: translate(-50%, -50%) scale(0);
	text-align: center;
	background-color: white;
	-webkit-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	display: none;
}
.floatButton p {
	position: absolute;
	left: 50%;
	transform: translate(-50%);
	margin: 0;
	margin-top: 5px;
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.floatButton img {
	margin-top: 5px;
	width: 40px;
}

#addButton svg {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
#addFoodBG {
	position: fixed;
	top: 0;
	left: 0;
	margin: 0;
	width: 100vw;
	height: 100vh;
	z-index: 3;
	background-color: #f1f1e6;
	opacity: 0.95;
	display: none;
	opacity: 0;
	touch-action: none;
}
#dateSelector {
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: var(--max-width);
	min-width: 320px;
}
#dateSelector p {
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
	color: rgb(100, 100, 100);
	direction: ltr;
	margin: 20px 0;
	transition: margin 500ms;
}
.dateArrow {
	margin: 10px;
	width: 20px;
	height: 20px;
	cursor: pointer;
	fill: var(--main-color);
}
#diaryContainer {
	white-space: nowrap;
	overflow-x: hidden;
}
@media (max-width: 400px) {
	.floatButton img {
		margin-top: 8px;
		width: 30px;
	}
	.circleButton {
		width: 45px;
		height: 45px;
	}
	#addButton svg {
		width: 18px;
		height: 18px;
	}
	.dateArrow {
		width: 18px;
		height: 18px;
	}
}

/************** diary **************/

#pageBody {
	/* margin-left: 20px; */
	display: inline-block;
	width: 100%;
	max-width: var(--max-width);
	min-width: 320px;
}

.mealName {
	background-color: #ffffff;
	color: #1f1f1e;
	width: 100%;
	margin-top: 20px;
	display: inline-table;
	vertical-align: middle;
	text-align: right;
	box-sizing: border-box;
	border-bottom: 1px solid #f3f3f3;
	-webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.mealName h3 {
	margin-top: 5px;
	margin-bottom: 5px;
	margin-right: 18px;
	font-weight: normal;
	color: #2b2b2b;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
}
.mealFoods {
	position: relative;
	width: 100%;
	height: 105px;
	color: #1f1f1e;
	box-sizing: border-box;
	background-color: #fafaf5;
	-webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	cursor: pointer;
}
.mealFoods p {
	margin: 0;
	color: #2b2b2b;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
}
.mealFoods img {
	width: 50px;
}
.mealFoodsImgP {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

@media (max-width: 400px) {
	.mealFoods img {
		width: 45px;
	}
}

/************** NavicationBar **************/
#navicationBar {
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translate(-50%);
	margin: 0;
	height: 70px;
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	text-align: center;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: white;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
	padding-top: 5px;
	z-index: 1;
	box-shadow: 0px -5px 10px -5px #00000040;
}
.navElements {
	cursor: pointer;
}
.navElements p {
	margin: 0;
	color: #7d7d7d;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
}
.navElements svg {
	width: 20px;
	height: 20px;
	fill: #7d7d7d;
}
.navElements:hover svg {
	fill: black;
}
.navElements:hover p {
	color: black;
}

@media (max-width: 450px) {
	.navElements svg {
		width: 18px;
		height: 18px;
	}
	#navicationBar {
		height: 55px;
	}
	#addButton {
		bottom: 70px;
	}
}
/************** SearchPage **************/
#SearchPage {
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	margin: auto;
	transition: bottom 250ms, opacity 250ms;
}
#searchPageHeader {
	position: relative;
	width: 100%;
	height: 120px;
	background-color: var(--main-color);
}
#searchPageBack {
	position: absolute;
	top: 25%;
	left: 20px;
	transform: translate(0, -50%);
	width: 35px;
	height: 35px;
	fill: white;
	cursor: pointer;
}
#searchPageBack:hover {
	fill: black;
}
#searchBar {
	position: absolute;
	top: 60px;
	left: 50%;
	transform: translate(-50%);
	width: 75%;
	height: 40px;
	padding-right: 50px;
	padding-left: 10px;
	border: none;
	border-radius: 10px;
	box-shadow: 0px 0px 10px 0px #00000040;
	background-image: url(/assets/search.png);
	background-repeat: no-repeat;
	background-size: 20px 20px;
	background-position: calc(100% - 20px) 50%;
	font-size: 18px;
	font-family: "Cairo", sans-serif;
}
#searchBar::placeholder {
	font-size: 18px;
	font-family: "Cairo", sans-serif;
}
#searchPageHeaderTitle {
	position: absolute;
	right: 50%;
	top: 25%;
	text-align: right;
	width: 75%;
	transform: translate(50%, -50%);
	color: white;
	margin: 0;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
#searchOption {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-column-gap: 10px;
	grid-row-gap: 10px;
	margin: 10px;
	border-radius: 10px;
}
#searchOption p {
	display: grid;
	padding: 15%;
	justify-items: center;
	align-self: stretch;
	justify-self: stretch;
	margin: 0;
	cursor: pointer;
	font-weight: bold;
	background-color: white;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	color: #2b2b2b;
	border-radius: 3px;
}
#searchOption p:hover {
	background-color: var(--main-color);
	color: white;
}
#searchOption svg {
	margin: 15px 0;
	fill: var(--main-color);
}
#searchOption p:hover svg {
	fill: white;
}
@media (max-width: 450px) {
	#searchPageBack {
		width: 30px;
		height: 30px;
	}
}

/************** CategoriesList **************/
#categoriesListPage {
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	margin: auto;
	transition: bottom 300ms;
}
#categoriesListPageHeader {
	position: sticky;
	top: 0;
	z-index: 1;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 70px;
	background-color: var(--main-color);
}
#categoriesListPageBack {
	margin-left: 20px;
	width: 35px;
	height: 35px;
	fill: white;
	cursor: pointer;
}
#categoriesListPageBack:hover {
	fill: black;
}
#categoriesListPageHeaderTitle {
	color: white;
	margin: 0;
	margin-right: 30px;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
#categoriesListBody {
	list-style: none;
	padding: 0;
	margin-top: 5px;
	width: 100%;
	max-width: var(--max-width);
	min-width: 320px;
}
#categoriesListBody li {
	position: relative;
	background-color: white;
	font-size: calc(17px + 6 * ((100vw - 320px) / 680));
	cursor: pointer;
	margin-bottom: 5px;
	padding: 10px;
	padding-right: 45px;
	border-radius: 10px;
	margin-left: 10px;
	margin-right: 10px;
	color: #2b2b2b;
}
#categoriesListBody li:hover {
	background-color: var(--main-color);
	color: white;
}
#categoriesListBody li:hover::before {
	background-image: url(/assets/dotHover.svg);
}
#categoriesListBody li::before {
	content: " ";
	display: inline-block;
	position: absolute;
	top: 50%;
	right: 16px;
	transform: translate(0, -50%);
	background-image: url(/assets/dot.svg);
	background-size: 16px 16px;
	height: 16px;
	width: 16px;
}
@media (max-width: 450px) {
	#categoriesListPageBack {
		width: 30px;
		height: 30px;
	}
}
